.notes-icon {
    font-size: 40px;
}

input {
    border: none;
    outline: none;
}

input:active {
    border: none;
    outline: none;
}

textarea {
    width: 50%;
    height: 100px;
    border: none;
    outline: none;
}

textarea:active {
    border: none;
    outline: none;
}

form {
    width: auto;
    padding: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    box-shadow: none;
    border: none;
    height: 46px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color: transparent;
}

.button span {
    color: black;
}

.delete-icon {
    background: none;
    border: none;
}

.Note {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

footer {
    position: fixed;
    bottom: 0;
    left: 45%;
}